
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import {mapGetters,mapMutations} from 'vuex';
import util from "../util";

export default {
data() {
return {
pageShow: false,
messageBadge: "80",
cellArr: [
{
title: "微信",
lefticonclass: "icon-weixin",
righticonclass: "icon-xuanzhong"
},
{
title: "支付宝",
lefticonclass: "icon-zhifubao",
righticonclass: "icon-weixuanzhong"
}
],
popup: {
popupState: false,
type: "reward"
},
previewList: {
list: [],
index: 0,
userId: ''
},
rewards: {
list: [0.01, 0.1, 0.5, 1, 5, 10],
seleted: -1
},
confirmReward: "",
tab: {
tabList: [
{topics_id: "0", tits: "全部"}
],
id: 0
},
baseList: [],
designList: []
};
},
computed: {
...mapGetters([
'userInfo'
]),
},
created: function() {
let userInfo = localStorage.getItem("userInfo");
if(userInfo) {
this.updateUserInfo(JSON.parse(userInfo));
}
},
mounted: async function() {
this.$vux.loading.show({
text: "Loading"
});
/*
** 话题列表
*/
let tabList = await util.getData({
url: "/materials/topicslists",
method: "post"
});
this.tab.tabList = this.tab.tabList.concat(tabList);
this.tab.id = this.tab.tabList[0].topics_id;
/*
** 轮播图
*/
this.bannerList = await util.getData({
url: "/materials/bannerslists",
method: "post"
});
this.bannerList.forEach((item, index) => {
item = {
img: item.thumbs
};
this.baseList.push(item);
});
/*
** 随手拍列表
*/
let designListUrl;
if(this.userInfo) {
designListUrl = `/materials/postslists?loginid=${this.userInfo.id}&topics_id=${this.tab.id}&pages=1&rowsmax=10`
}else {
designListUrl = `/materials/postslists?topics_id=${this.tab.id}&pages=1&rowsmax=10`
}
this.designList = await util.getData({
url: designListUrl,
method: "get"
});

this.designList.forEach((item, index) => {

item.previewList = [];
item.images.forEach(img => {
item.previewList.push({
id: img.id,
img: img.updatas,
collected: img.collected
});
});

item.sex = item.gender == 1 ? "boy" : "girl";

switch (item.previewList.length) {
case 1:
item.class = "one-img";
break;
case 2:
item.class = "two-img";
break;
case 3:
item.class = "three-img";
break;
case 4:
item.class = "four-img";
break;
case 5:
item.class = "five-img";
break;
case 6:
item.class = "fix-img";
break;
case 7:
item.class = "seven-img";
break;
case 8:
item.class = "eight-img";
break;
case 9:
item.class = "nine-img";
break;

default:
break;
}
});
this.$vux.loading.hide();
this.pageShow = true;
},
methods: {
...mapMutations({
updateUserInfo: 'updateUserInfo'
}),

async seleted(id) {
this.tab.id = id;
this.$vux.loading.show({
text: "Loading"
});
/*
** 随手拍列表
*/
this.designList = await util.getData({
url: `/materials/postslists?topics_id=${
this.tab.id
}&pages=1&rowsmax=10`,
method: "get"
});

this.designList.forEach((item, index) => {

item.previewList = [];
item.images.forEach(img => {
item.previewList.push({
img: img.updatas
});
});

item.sex = item.gender == 1 ? "boy" : "girl";

switch (item.previewList.length) {
case 1:
item.class = "one-img";
break;
case 2:
item.class = "two-img";
break;
case 3:
item.class = "three-img";
break;
case 4:
item.class = "four-img";
break;
case 5:
item.class = "five-img";
break;
case 6:
item.class = "fix-img";
break;
case 7:
item.class = "seven-img";
break;
case 8:
item.class = "eight-img";
break;
case 9:
item.class = "nine-img";
break;

default:
break;
}
});
this.$vux.loading.hide();
},
async imgCollect(imgs,userId) {
let data = await util.request({
url: `/materials/collects`,
method: "post",
param: {
loginid: this.userInfo.id,
users_mains_id: userId,
targets_id: imgs.id,
types: 1
}
});
if(data.return_code == 'success') {
this.$vux.toast.show({
text: '收藏成功'
});
}else {
this.$vux.alert.show({
title: '提示',
content: data.return_data
});
}
},
show(index, designItem) {
this.previewList.index = index;
this.previewList.list = designItem.previewList;
this.previewList.userId = designItem.userid;
},
previewClose() {
this.previewList.list = [];
},
toDetail(designItem) {
this.$router.push({
path: "/page/take_photo/DesignDetail",
query: { id: designItem.sources_id }
});
},
togglePopup(type) {
this.popup.type = type;
this.popup.popupState = !this.popup.popupState;
},
rewardSelected(index) {
this.confirmReward = this.rewards.list[index];
this.rewards.seleted = index;
this.toPay();
},
toPay() {
this.popup.type = "pay";
},
backSelectReward() {
this.confirmReward = "";
this.popup.type = "reward";
},
selectPay(cell) {
this.cellArr.forEach((item, index) => {
item.righticonclass = "icon-weixuanzhong";
});
cell.righticonclass = "icon-xuanzhong";
},
async follow(users) {
let types = users.followed ? 0 : 1;
if (this.userInfo) {
let data = await util.request({
url: `/materials/usersfollows`,
method: "post",
param: {
users_mains_id: users.userid,
loginid: this.userInfo.id,
types: types
}
});
if (data.return_code == "success") {
users.followed = types;
this.designList.forEach((item, index) => {
if ((users.userid = item.userid)) {
item.follow = users.follow;
}
});
}
this.$vux.toast.show({
text: data.return_data
});
}else {
this.$vux.confirm({
title: '提示',
content: '请先登录',
onConfirm () {
this.$router.push({
path: '/page/user/LoginIndex'
});
}
});
}
}
},
components: {
XHeader,
Swiper,
XButton,
Flexbox,
FlexboxItem,
XImg,
Popup,
XInput,
Card,
Cell,
Badge
},
watch: {}
};



